body {
  background-color:#F0F1F3;
  margin: 0;

}
.login-container {
    width: 400px;
    margin: auto;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
    position:absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
 
  .form-title {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    color: #34519a;
    font-family: Inter;
    font-size: 30px;
    font-weight: bold;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  .form-control{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px 14px;
    width: 360px;
    height: 44px;
    background: #ffffff;
    border: 1px solid #cfd4dc;
    border-radius: 8px;
  }
  .mb-3 {
    margin-bottom: 1rem;
  }
   
  .form-label {
    color: #48505e;
    font-weight: bold;
    font-family: Inter;
    font-size: 15px;
    font-weight: medium;
    line-height: 20px;
  }
  
  .login-btn {
    width: 100%;
    margin-top: 10px;
    padding: 10px 0;
    font-size: 18px;
    border-radius: 5px;
    border: none;
    transition: background-color 0.2s ease-in-out;
    background: #1366d9;
    border: 1px solid #1366d9;
    border-radius: 4px;
    color: white;
  }
  .login-btn:hover {
    background-color: #2858d1;
  }
  .register-link {
    color:#1366d9;
  }
  
.Captcha_div{
  display: flex;
  justify-content: center;
}
.login-intro{
  display: flex;
  justify-content: center;
  padding-bottom: 1rem;
  
}

  