.profile-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #f2f2f2;
  padding: 20px;
  font-family: 'Roboto', sans-serif;
}

.profile-section {
  width: 90%;
  max-width: 600px;
  margin-bottom: 40px;
  padding: 40px;
  background-color: #fff;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

.profile-title {
  font-size: 2.5rem;
  color: #14213d;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.profile-info p {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 10px;
}

.edit-profile-form {
  display: flex;
  flex-direction: column;
}

.profile-group {
  margin-bottom: 20px;
}

.profile-label {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 5px;
}

.profile-input {
  padding: 10px;
  font-size: 1.2rem;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.edit-profile-btn,
.save-profile-btn,
.cancel-edit-btn,
.change-password-btn,
.save-password-btn {
  background-color: #14213d;
  color: #fff;
  border: none;
  padding: 10px 20px;
  font-size: 1.2rem;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-right: 10px;
}

.edit-profile-btn:hover,
.save-profile-btn:hover,
.cancel-edit-btn:hover,
.change-password-btn:hover,
.save-password-btn:hover {
  background-color: #173d70;
  transform: scale(1.1);
}
