.nav {
  background-color: #14213d;
  height: 100px;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.5);
  left: 0; 
  right: 0; 
}

.navbar-title,.navbar-contents{
  display: flex;
  justify-content: center;
}


.navbar-title{
  padding: 1rem;
}

.navbar-logo {
  color: #ffeaea;
  font-size: 2rem;
  display: flex;
  align-items: center;
  justify-self: flex-start;
  cursor: pointer;
  font-weight: bold;
  text-decoration: none;
}

@media screen and (max-width: 768px) {
  .navbar-logo {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 480px) {
  .navbar-logo {
    font-size: 1rem;
  }
}

.menu-icon {
  display: none;
}

.nav-menu {
  display: flex;
  align-items: center;
  list-style: none;
  text-align: center;
  margin: 0;
  padding: 0;
}

.nav-item {
  height: 80px;
  margin: 0;
}

.nav-links {
  color: #fff;
  text-decoration: none;
  padding: 0 1rem;
  height: 100%;
  display: flex;
  align-items: center;
  transition: all 0.3s ease-out;
}

.nav-links:hover {
  color: #1f1f1f;
  width: auto;
  height: auto;
  background-color: #fff;
  border-radius: 10px;
}

.activated {
  color: #1f1f1f;
  width: auto;
  height: auto;
  border-radius: 10px;
  background-color: #fff;
}

@media screen and (max-width: 960px) {
  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #fff;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    height: 100vh;
    position: fixed;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    background-color: #1f1f1f;
    width:100%;
    padding-top: 2rem;
  }

  .nav-menu.active {
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    width: 100%;
    text-align: center;
    padding: 1rem;
    margin: 0.5rem 0;
    transition: all 0.3s ease-out;
    border-radius: 10px;
  }

  .nav-links:hover {
    color: #1f1f1f;
    background-color: #fff;
    border-radius: 10px;
  }
}