.ProductList-Container {
  background-color: white;
  border-radius: 10px;
  margin: 10px;
  flex-wrap: wrap;
  overflow: auto;
}

.Product-Buttons {
  display: flex;
  padding: 10px;
  align-items: center;
  justify-content: flex-end;
}

.product-table {
  border-collapse: collapse;
  width: 100%;
  max-width: 100%;
  font-family: "PT Serif";
  font-size: 14px;
  color: #667085;

}
.product-table thead th {
  background-color: white;
  font-weight: 600;
  padding: 12px 16px;
  text-align: left;
  color: #667085;
  border-bottom: 0.5px solid #d0d3d9;
}

.Product-Heading {
  display: flex;
  justify-content: space-between;
  flex-shrink: 0;
}


.List-title {
  align-self: center;
  margin-left: 50px;
  font-size: 30px;
  font-weight: bold;
}

.product-table tbody td {
  vertical-align: middle;
  padding: 12px 16px;
  border-bottom: 0.5px solid #d0d3d9;
}
.AddProduct-Button {
  color: white;
  background-color: #1366d9;
  border-radius: 5px;
  padding: 5px 20px 5px 20px;
  border: 0.1px white solid;
  max-height: 50px;
}
.AddProduct-Button:hover {
  background-color: #0a4ca8;
  transform: translateY(-2px);
}

.RemoveProduct-Button {
  color: white;
  background-color: #d91313;
  border-radius: 5px;
  padding: 5px 20px 5px 20px;
  border: 0.1px white solid;
  max-height: 50px;
}
.RemoveProduct-Button:hover {
  background-color: #d91313;
  transform: translateY(-2px);
}

.EditProduct-Button {
  color: white;
  background-color: #2cc024;
  border-radius: 5px;
  padding: 5px 20px 5px 20px;
  border: 0.1px white solid;
  max-height: 50px;
}
.EditProduct-Button:hover {
  background-color: #2cc024;
  transform: translateY(-2px);
}

.search-bar-container .searchby-container {
  display: flex;
}
.search-bar-container {
  background-color: white;
  margin: 30px;
  padding: 10px;
  border-radius: 10px;
  border: 0.2px solid #d0d3d9;
  width: 1000px;
}

.searchby-select {
  justify-self: center;
  align-self: center;
  border: 0px solid;
  font-size: 12px;
}
.search-bar {
  border: 0px solid;
  padding-left: 10px;
  flex: 1;
}

.SearchProduct-Button {
  color: rgb(255, 255, 255);
  font-weight: bold;
  font-size: 15px;
  background-color: #75cab8;
  border-radius: 5px;
  padding: 10px 100px 10px 100px;
  border: 0.1px white solid;
  max-height: 50px;
}

.SearchProduct-Button:hover {
  background-color: #4f8b7e;
  transform: translateY(-2px);
}

.pagination-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 15px;
}

.pagination {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.pagination li {
  margin: 0 4px;
}

.pagination button {
  background-color: transparent;
  border: none;
  color: #aea7bb;
  cursor: pointer;
  font-size: 15px;
  font-weight: bold;
}

.pagination button:hover {
  color: #000000;
}

.pagination button.active {
  color: #000000;
  border: 1px black solid;
  border-radius: 30px;
  padding: 8px 12px;
}

.items-per-page-container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  margin-right: 20px;
}

.items-per-page-label {
  font-size: 15px;
  margin-right: 20px;
}

.items-per-page-select {
  padding: 8px 16px;
  font-size: 15px;
  border: none;
  border-radius: 4px;
  background-color: #f5f5f5;
  cursor: pointer;
}

.items-per-page-select:focus {
  outline: none;
  box-shadow: 0 0 0 2px #007bff;
}

.items-per-page-option {
  font-size: 14px;
  padding: 8px 16px;
  cursor: pointer;
}

.items-per-page-option:hover {
  background-color: #007bff;
  color: #fff;
}

.AddNewButtons {
  display: flex;
  justify-content: space-between;
  margin: 10px 0px;
}

.addproduct-container {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(93, 91, 91, 0.099);
  display: flex;
  align-items: center;
  justify-content: center;
}


.addCategory-container{
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background-color: rgba(93, 91, 91, 0.466);
  display: flex;
  align-items: center;
  justify-content: center;
}

.addproduct-content {
  display: flex;
  width: 400px;
  background-color: white;
  border-radius: 20px;
  border: 5px solid white;
  transform: scale(0.9); 
  padding: 10px;

}

.AddNewButtons .AddProduct-Button {
  flex: 1;
  height: 30px;
}
.AddNewButtons .EditProduct-Button {
  flex: 1;
  height: 30px;
}
.AddNewButtons .RemoveProduct-Button {
  flex: 1;
  height: 30px;
}

.addproduct-container .input{
  height: 1px;
}

.custom-mb-3{
  display: flex;
  padding: 10px 0px 10px 0px;
}
.custom-label {
  display: inline-block;
  width: 50%; /* Adjust the width as needed */
}

.custom-checkbox {
  width: 50%; /* Adjust the width as needed */
  margin-left: 1rem; /* Add margin as needed */
}
.addproduct-container .mb-3{
  margin-bottom: 3px;
}



@media (max-width: 1060px) {
  .Product-Heading {
    flex-wrap: wrap;
  }
  .product-table {
    table-layout: fixed;
    width: 100%;
  }
  
  .product-table td,
  .product-table th {
    white-space: normal;
    overflow: visible;
  }
  .product-table thead th, .product-table td {
    font-size: 2vw;
  }
  
  .product-table td span,
  .product-table th span {
    display: block;
    overflow: visible;
    white-space: normal;
    text-overflow: clip;
  }
  .AddProduct-Button,
  .RemoveProduct-Button,
  .EditProduct-Button {
    padding: 3px 12px;
    font-size: 12px;
  }
  .addproduct-content {
    transform: scale(0.7); 
  }
}


.product-details-heading {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  justify-content: center;
  display: flex;
  padding-bottom: 5px;
  border-bottom: 1px solid black;
}


.product-details-info {
  margin-bottom: 3px;
  justify-content: center;
  display: fe;
}

.product-details-info:first-of-type {
  margin-top: 10px;
}
.product-details-info:last-of-type {
  margin-bottom: 15px;
}



.moredetailsButton-container {
  padding: 0;
}

.moredetailsButton {
  background-color: transparent;
  border: none;
  color: #000;
  cursor: pointer;
  font-size: 20px;
  font-weight: bold;
}
